<template lang="pug">
.page-second-hand-car-editor
    my-page-title(title="新增二手车")
    el-tabs(v-model="active")
        el-tab-pane(label="基本信息" name="0")
            my-car-base-info
        el-tab-pane(label="车辆描述" name="1")
            my-car-desc
        el-tab-pane(label="基本参数" name="2")
            my-car-base-params
        el-tab-pane(label="发动机参数" name="3")
            my-car-engine-params
        el-tab-pane(label="底盘参数" name="4")
            my-car-chassis-params
        el-tab-pane(label="车轮制动" name="5")
            my-car-brake-params
        el-tab-pane(label="手续信息" name="6")
            my-car-other-params
        el-tab-pane(label="车辆图片" name="7")
            my-image-list-uploader
</template>

<script>
import Desc from '../components/carForms/Desc';
import BaseInfo from '../components/carForms/BaseInfo';
import BaseParams from '../components/carForms/BaseParams';
import EngineParams from '../components/carForms/EngineParams';
import ChassisParams from '../components/carForms/ChassisParams.vue';
import BrakeParams from '../components/carForms/BrakeParams.vue';
import OtherParams from '../components/carForms/OtherParams.vue';
import ImageListUploader from '@/components/ImageListUploader';
export default {
    data () {
        return {
            active: '0'
        };
    },
    components: {
        'my-car-desc': Desc,
        'my-car-base-info': BaseInfo,
        'my-car-base-params': BaseParams,
        'my-car-engine-params': EngineParams,
        'my-car-chassis-params': ChassisParams,
        'my-car-brake-params': BrakeParams,
        'my-car-other-params': OtherParams,
        'my-image-list-uploader': ImageListUploader
    }
};
</script>



