<template lang="pug">
.comp-car-base-info
    el-form(label-width="100px" label-position="left")
        el-form-item(label="车商")
            el-input.form-item-1()
        el-form-item(label="标题")
            el-input.form-item-2()
        el-form-item(label="卖价")
            el-input-number.form-item-1(controls-position="right")
            span.unit 万元
        el-form-item(label="新车价")
            el-input-number.form-item-1(controls-position="right")
            span.unit 万元
        el-form-item(label="新车购置税")
            el-input-number.form-item-1(controls-position="right")
            span.unit 万元
        el-form-item(label="上牌时间")
            el-date-picker.form-item-1(type="month")
        el-form-item(label="行驶里程")
            el-input-number.form-item-1(controls-position="right")
        el-form-item(label="归属地")
            el-select.form-item-1(v-model="test")
                el-option(label="成都" value="1")
        el-form-item(label="排放标准")
            el-select.form-item-1(v-model="test")
                el-option(v-for="(item, index) in gas" :label="item" :value="index" :key="index")
        el-form-item(label="")
            my-button(type="primary") 保存
</template>

<script>
import { gas } from '../../lib/carConfig';
export default {
    data () {
        return {
            gas: gas,
            test: '',
            form: {
                pf: '',
                location: ''
            }
        };
    }
};
</script>



